
import XHeader from 'vux/src/components/x-header/index.vue'

import AlertPlugin from 'vux/src/plugins/alert/index.js'

export default {
components: {
XHeader,
AlertPlugin
},
data() {
return {
type_time: null,  //投诉时间
type_site: null,           //投诉位置

type_list: {},
duty: {},
typeList: null,
manageName: null,
section: null, //所属部门
name: null,  // 姓名
explain: null,  // 问题描述
way: null,  // 处理方式
suggest: null,  // 建议/补充
id: null,
datas: [],
allOver: false,    // 详情
titleTxt: null,
}
},
mounted(){
this.id = this.$route.query.id;
console.log("我获取的ID是,"+this.id)
this.getInfo();
},
methods:{

overSolve(){
this.allOver = !this.allOver;
},
getInfo(){
this.common.commonFun.ajax({
dom: this,
url: this.common.apiUrl.reasonDetails,
type: "get",
data: {
id: this.id,
},
success: (response, allResponse) => {

console.log('response',response);
this.type_site = response.area_name + response.weizhi;
this.type_list = response.waiter;
this.manageName = response.deal_user;
this.duty = response.duty;
this.typeList = response.abnormal_type_name;
this.datas = response.data;
this.explain = response.desc;
this.way = response.abnormal_res;
this.titleTxt = response.add_remark || '请输入补充意见';
this.type_time = response.hujiao_time
}
});
},

back(){
if(this.$route.params.complaint == 'complaint'){
this.$router.go(-1)
}else{
this.$router.push({
name:"call",
params:{
headValue: 1,
abnormal: 1
}
})
}
},

submit(){

this.common.commonFun.ajax({
dom: this,
url: this.common.apiUrl.reasonDetailPOST,
type: "post",
data: {
id: this.id,
add_remark: this.suggest,
},
success: (response, allResponse) => {

if( allResponse.code == 0 ){
this.$vux.toast.text(allResponse.msg, 'middle')
setTimeout(() => {
if(this.$route.params.complaint == 'complaint'){
this.$router.go(-1)
}else{
this.$router.push({
name:"call",
params:{
headValue: 1,
abnormal: 1
}
})
}
}, 2100)
}
if( allResponse.code == 1 ){
this.$vux.toast.text(allResponse.msg, 'middle')
}
}
});
}
},
}



